.app-inner-wrap {
  width: 85%;
  max-width: 40em;
  margin: 0 auto;
  text-align: left;
}
.label {
  display: block;
  margin-bottom: 0.4em;
}
textarea, input {
  margin-bottom: 0.8em;
  width: 100%;
  border: solid 1px var(--line);
  border-radius: 0.4em;
  padding: 0.32em 0.5em;
}
textarea {
  resize: vertical;
}
.result-wrap {
  margin-top: 2em;
  padding-top: 0.8em;
  border-top: solid 1px var(--line);
}
.result-label {
  line-height: 1;
  margin-bottom: 0;
}
.result {
  margin-top: 0;
  font-size: 2.4em;
  font-weight: 700;
}
