body {
  margin: 0;
  font-family: 'IBM Plex Mono', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-weight: 500;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  --medium: #333333;
  --dark: #111111;
  --light: #999999;
  --line: #DDDDDD;
}

h1 {
  font-size: 3.2em;
  font-weight: 700;
}
p, textarea, input, label {
  font-family: 'IBM Plex Mono', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-size: 1em;
  color: #333333;
}
button {
  font-weight: 700;
  font-size: 1em;
  color: #FFFFFF;
  border: none;
  background-color: var(--medium);
  padding: 0.6em 1.6em;
  border-radius: 0.4em;
  margin-top: 0.4em;
  cursor: pointer;
}
